@import '../Five-day-forecast/styles.css';

.component-weather-widget {
	font-size: 0.7em;
}

.weather-city {
	text-align: center;
	padding-bottom: 0.5em;
	margin-bottom: 0.75em;
	position: relative;
}

.weather-city::after {
	content: '';
	right: 0;
	position: absolute;
	bottom: 0;
	left: 0;
	background: white;
	border-radius: 1em;
	height: 3px;
	-webkit-box-shadow: 0 0 1em #1041FF,
	0 0 3em #1041FF,
	0 0 10em #1041FF,
	0 0 10em #1041FF,
	0 0 .4em #8BFDFE,
	.05em .1em .1em #147280;
	        box-shadow: 0 0 1em #1041FF,
	0 0 3em #1041FF,
	0 0 10em #1041FF,
	0 0 10em #1041FF,
	0 0 .4em #8BFDFE,
	.05em .1em .1em #147280;
}

.current-weather {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

@media (min-width: 768px) {
	.current-weather {
		-ms-flex-pack: distribute;
		    justify-content: space-around;
	}
}

